nav {
    font-family: 'Playfair Display', serif;
    letter-spacing: .2em;
    font-size: 1.05em;
    font-weight: bolder;
    position: -webkit-sticky;
    position: sticky;
    text-align: right;
    top: 3em;
    padding-right: 50px;
    z-index: 100;
}

nav li {
    display: inline-block;
    cursor: pointer;
}

nav li .link {
    text-decoration: none;
    color: #383838;
    padding: 1em;
    opacity: 0.5;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

li .link:hover {
    opacity: 1;
}

.active {
    color: #464646;
    opacity: 10;
}

/* Mobile Devices */

@media only screen and (max-width: 768px) {
    nav {
        position: fixed;
        width: 100%;
        top: 0;
    }
    ul {
        display: flex;
    }
}
.landingText {
    position: center;
    flex-direction: row;
    font-family: 'Yeseva One', cursive;
    font-size: 4em;
    padding: 5em 0em 9em 0em;
}

.minorText {
    font-size: 30%;
    font-family: 'Playfair Display', serif;
    color: gray;
}

/* Mobile Devices */

@media only screen and (max-width: 768px) {
    .landingText {
        font-size: 40px;
        padding: 9em 0em 9em 0em;
    }
}
.about {
    content: "";
    clear: both;
    display: flex;
    flex-direction: row;
    padding: 0em 10em 12em 10em;
}

.aboutText {
    position: relative;
    font-family: 'Playfair Display', serif;
    text-align: unset;
}

.aboutText h1 {
    font-size: 3em;
    padding-bottom: 20px;
    color: #2a2a2a;
    text-align: left;
    letter-spacing: .1em;
    font-family: 'Yeseva One', cursive;
}

.aboutText p {
    font-size: 1em;
    text-align: left;
}

.about img {
    float: right;
    height: auto;
    width: 30%;
    padding: 0 0 6px 60px;
}

.available {
    padding-top: 79px;
    font-family: 'Yeseva One', cursive;
    color: #2a2a2a;
}

a.open {
    color: #464646;
    text-decoration: none;
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
}

a.open:hover {
    box-shadow: 0 10px 0 0 rgba(0, 0, 0, 0.19);
}

.open {
    font-size: 1em;
    letter-spacing: .1em;
    text-transform: uppercase;
    display: inline-block;
    text-align: center;
    width: 5em;
    font-weight: bold;
    padding: 5px 0px;
    border-radius: 2px;
    position: relative;
    box-shadow: 0px 3px 1px 0px rgba(0, 0, 0, 0.1);
}

/* Mobile Devices */

@media only screen and (max-width: 768px) {
    .about {
        padding: 0em 2em 12em 2em;
    }
    .aboutText h1 {
        font-size: 35px;
        display: flex;
    }
    .about img {
        width: 50%;
        padding: 0 0 6px 20px;
    }
    .aboutText p {
        display: block;
    }
}
.project {
    box-shadow: black;
}

.project img {
    width: 20em;
    height: 20em;
}

a {
    color: #464646;
    text-decoration: none;
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
}
.portfolio {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0em 10em 12em 10em;
    /* background-image: url('./paint2.png');
    background-repeat:no-repeat;
    background-position: right;
    background-size: cover; */
}

.portfolio .heading {
    font-size: 3em;
    color: #2a2a2a;
    padding-bottom: 30px;
    letter-spacing: .1em;
    text-align: left;
    font-family: 'Yeseva One', cursive;
}

.portfolio .portfolio-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1em;
    font-family: 'Playfair Display', serif;
}

.portfolio img {
    box-shadow: black;
}

.other {
    font-family: 'Yeseva One', cursive;
    padding: 70px 0px 20px 0px;
}

.repo-btn {
    align-items: center;
    font-family: 'Playfair Display', serif;
}

a.bttn {
    color: #464646;
    text-decoration: none;
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
}

a.bttn:hover {
    box-shadow: 0 10px 0 0 rgba(0, 0, 0, 0.19);
}

.bttn {
    font-size: 1em;
    letter-spacing: .1em;
    text-transform: uppercase;
    display: inline-block;
    text-align: center;
    width: 15em;
    font-weight: bold;
    padding: 14px 0px;
    border-radius: 2px;
    position: relative;
    box-shadow: 0px 3px 1px 0px rgba(0, 0, 0, 0.1);
}

/* Mobile Devices */

@media only screen and (max-width: 768px) {
    .portfolio {
        padding: 0em 2em 12em 2em;
    }
    .portfolio .heading {
        font-size: 35px;
        display: flex;
    }
    .project img {
        width: 10em;
        height: 10em;
    }
    .portfolio .portfolio-content {
        grid-template-columns: none;
    }
    .other {
        font-size: 20px;
    }
    .bttn {
        font-size: 15px;
    }
}
.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
